<template>
  <scroller>
    <div style="padding:20px;">
      <header>
        <i class="iconfont icon-image"></i>img<span class="vue-green">Vue</span>r
      </header>
      <div class="descript">
        An image viewer base on Vue.js for mobile
      </div>
      <div class="subtitle">Multiple</div>
      <div style="overflow:hidden">
        <img v-gallery:group1
          class="thumbnail"
          v-for="item in list1"
          :src="item.src"
          :key="item.src + 1">
      </div>
      <p>
        <pre>&lt;img 
      <span class="vue-green">v-gallery:</span>groupName1 
      v-for="item in list1" 
      :src="item.src"&gt;</pre>
      </p>
      <div style="overflow:hidden">
        <img v-gallery:group2
          class="thumbnail"
          v-for="item in list2"
          :src="item.src"
          :key="item.src + 2">
      </div>
      <p>
        <pre>&lt;img 
      <span class="vue-green">v-gallery:</span>groupName2 
      v-for="item in list2" 
      :src="item.src"&gt;</pre>
      </p>
      <div class="subtitle">Single</div>
      <div style="overflow:hidden">
        <img v-gallery
          class="thumbnail"
          v-for="item in list2"
          :src="item.src"
          :key="item.src + 3">
      </div>
      <p>
        <pre>&lt;img 
      <span class="vue-green">v-gallery</span>
      v-for="item in list2" 
      :src="item.src"&gt;</pre>
      </p>
      <footer @click="toGitHub"><i class="iconfont icon-github"></i>GitHub</footer>
    </div>
  </scroller>
</template>

<script>
import Vuer from './Vuer.vue'
export default {
  components: { Vuer },
  data() {
    return {
      list1: [
        {
          src:
            'https://wx4.sinaimg.cn/mw690/686d7361ly1fp9a246f3pj21kw16okbj.jpg'
        },
        {
          src:
            'https://wx3.sinaimg.cn/mw690/686d7361ly1fp9a2496y3j21kw16otxy.jpg'
        },
        {
          src:
            'https://wx4.sinaimg.cn/mw690/686d7361ly1fp9a52e83uj21kw0w04kg.jpg'
        }
      ],
      list2: [
        {
          src:
            'https://wx2.sinaimg.cn/mw690/686d7361ly1fp99t4bem4j21kw16ohdu.jpg'
        },
        {
          src:
            'https://wx1.sinaimg.cn/mw690/686d7361ly1fp99v4u8jlj21kw1kwkjm.jpg'
        }
      ]
    }
  },
  methods:{
    toGitHub(){
      location.href='https://github.com/ssshooter/img-vuer'
    }
  }
}
</script>

<style scoped>
.thumbnail {
  display: block;
  float: left;
  margin: 3px;
  height: 100px;
  width: 100px;
  object-fit: cover;
}
pre {
  font-family: Monaco;
  color: #fff;
  padding: .5rem;
}
p{
  background: #4d4d4d;
}
header {
  font-size: 2rem;
  color: #fff;
}
footer{
  font-size: 1rem;
  color: #fff;
}
.descript {
  font-size: 1.3rem;
  color: #9a9a9a;
  margin-bottom: 2rem;
}
.subtitle{
  font-size: 1.2rem;
  color: #a7a8aa;
  margin-bottom: 1rem;
}
.vue-green {
  color: #4fc08d;
}
.iconfont{
  font-size: 1.5rem;
  margin: 0.3rem;
}
</style>



